import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import shangjialeixingList from '@/views/pages/shangjialeixing/list'
import shangjialeixingDetail from '@/views/pages/shangjialeixing/formModel'
import shangjialeixingAdd from '@/views/pages/shangjialeixing/formAdd'
import shangjiaxinxiList from '@/views/pages/shangjiaxinxi/list'
import shangjiaxinxiDetail from '@/views/pages/shangjiaxinxi/formModel'
import shangjiaxinxiAdd from '@/views/pages/shangjiaxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import fankuitousuList from '@/views/pages/fankuitousu/list'
import fankuitousuDetail from '@/views/pages/fankuitousu/formModel'
import fankuitousuAdd from '@/views/pages/fankuitousu/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'shangjialeixingList',
			component: shangjialeixingList
		}, {
			path: 'shangjialeixingDetail',
			component: shangjialeixingDetail
		}, {
			path: 'shangjialeixingAdd',
			component: shangjialeixingAdd
		}
		, {
			path: 'shangjiaxinxiList',
			component: shangjiaxinxiList
		}, {
			path: 'shangjiaxinxiDetail',
			component: shangjiaxinxiDetail
		}, {
			path: 'shangjiaxinxiAdd',
			component: shangjiaxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'fankuitousuList',
			component: fankuitousuList
		}, {
			path: 'fankuitousuDetail',
			component: fankuitousuDetail
		}, {
			path: 'fankuitousuAdd',
			component: fankuitousuAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
